import VueRouter from 'vue-router'
import AuthConst from './config/AuthConst'
import UsuariosService from "./services/UsuariosService";

const Login = () => import('./components/Login')
const Dashboard = () => import('./components/Dashboard')
const Alumnos = () => import('./components/Alumnos/Alumnos')
const Saldo = () => import('./components/Saldo/Saldo')
const Menu = () => import('./components/Menu/DashboardMenu')
const DashboardOrdenes = () => import('./components/Ordenes/DashboardOrdenes')
const DashboardTienda = () => import('./components/Tienda/DashboardTienda')
const HistorialOrdenes = () => import('./components/Historial/HistorialOrdenes')
const Boletines = () => import('./components/BoletinEscolar/DashboardBoletin')
const Carrito = () => import('./components/Carrito/ResumenCarrito')
const InformacionPersonal = () => import('./components/Inicio/InformacionPersonal')
const LoginCafeteria = () => import('./components/LoginCafeteria')

// Vistas para alumno
const VistaProductosParaAlumno = () => import('./components/UsuarioAlumno/VistaProductosParaAlumno')

// Vistas para cafeteria
const DashboardCafeteria = () => import('./components/UsuarioCafeteria/DashboardCafeteria')
const ProductosCafeteria = () => import('./components/UsuarioCafeteria/Productos/ProductosCafeteria')
const MenusCafeteria = () => import('./components/UsuarioCafeteria/Menus/MenusCafeteria')
const PanelDeBusqueda = () => import('./components/UsuarioCafeteria/BusquedaDeOrdenes/PanelDeBusquedaDeOrdenes')
const PanelBoletines = () => import('./components/UsuarioCafeteria/Boletines/PanelBoletines')
const Cafeterias = () => import('./components/UsuarioAdministrador/Cafeterias')
const Usuarios = () => import('./components/UsuarioAdministrador/Usuarios')

const routes = [
  { path: '/', component: Dashboard, name: 'Dashboard' },
  { path: '/login', component: Login, name: 'Login' },
  { path: '/alumnos', component: Alumnos, name: 'Alumnos' },
  { path: '/saldo', component: Saldo, name: 'Saldo' },
  { path: '/menu', component: Menu, name: 'DashboardMenu' },
  { path: '/ordenes', component: DashboardOrdenes, name: 'DashboardOrdenes', meta: {esUsuarioGestionadorOrdenes: true} },
  { path: '/tienda', component: DashboardTienda, name: 'DashboardTienda' },
  { path: '/historial', component: HistorialOrdenes, name: 'HistorialOrdenes' },
  { path: '/boletin', component: Boletines, name: 'DashboardBoletin' },
  { path: '/carrito', component: Carrito, name: 'ResumenCarrito', meta: {esUsuarioGestionadorOrdenes: true} },
  { path: '/informacion', component: InformacionPersonal, name: 'InformacionPersonal' },

  // Rutas de alumno
  { path: '/productos', component: VistaProductosParaAlumno, name: 'VistaProductosParaAlumno' },

  // Rutas de cafeteria
  { path: '/', component: DashboardCafeteria, name: 'DashboardCafeteria', meta: {esEncargado: true} },
  { path: '/busqueda-ordenes', component: PanelDeBusqueda, name: 'PanelDeBusquedaDeOrdenes', meta: {esEncargado: true} },
  { path: '/productos-cafeteria', component: ProductosCafeteria, name: 'ProductosCafeteria', meta: {esEncargado: true} },
  { path: '/menus-cafeteria', component: MenusCafeteria, name: 'MenusCafeteria', meta: {esEncargado: true} },
  { path: '/boletines', component: PanelBoletines, name: 'PanelBoletines', meta: {esEncargado: true} },
  { path: '/cafeterias', component: Cafeterias, name: 'Cafeterias', meta: {esEncargado: true} },
  { path: '/usuarios', component: Usuarios, name: 'Usuarios', meta: {esAdministrador: true} },
  { path: '/login-cafeteria', component: LoginCafeteria, name: 'LoginCafeteria' },





]

// FunciÃ³n para cargar el route
let getRouter = (vue) => {
  vue.use(VueRouter)
  const router = new VueRouter({ routes, mode: 'history' })

  router.beforeEach((to, from, next) => {
    const usuario = JSON.parse(localStorage.getItem(AuthConst.CURRENT_USER_KEY))
    if (usuario && usuario.id) {
      // Si el usuario ha iniciado sesiÃ³n
      if (to.name === 'Login') {
        next('/')
      } else if (to.matched.some(record => record.meta.esUsuarioGestionadorOrdenes)) {
        if (UsuariosService.usuarioPuedeGestionarOrdenes(usuario)) {
          next()
        } else {
          next('/')
        }
      } else if (to.matched.some(record => record.meta.esEncargado)) {
        if (usuario.esEncargado) {
          next()
        } else {
          next('/')
        }
      } else if (to.matched.some(record => record.meta.esAdministrador)) {
        if (usuario.esEncargado && usuario.administrador) {
          next()
        } else {
          next('/')
        }
      } else {
        next()
      }
    } else {
      // Si el usuario aÃºn no ha iniciado sesiÃ³n
      if (to.name !== 'Login') {
        next('/login')
      } else {
        next()
      }
    }
  })

  return router
}

export default { getRouter }
