const dayjs = require('dayjs');
require('dayjs/locale/es');
import weekOfYear from 'dayjs/plugin/weekOfYear';
dayjs.extend(weekOfYear)

const getFechaString = (fecha = new Date(), formato = 'YYYY-MM-DD') => {
  if (!fecha) return null

  return dayjs(fecha).startOf('day').locale('es').format(formato);
}

const getFechaToDate = (fecha) => {
  return dayjs(fecha).startOf('day').toDate();
}

const enRangoDentroDeSemanaActual = (fechaStr) => {
  // Boolean si fecha se encuentra dentro de la semana actual y no es sÃ¡bado (dÃ­a 6) ni domingo (dÃ­a 0)
  const fecha = dayjs(fechaStr);
  return fecha.day() > 0 && fecha.day() < 6 && fecha.week() === dayjs().week()
}

export default { getFechaString, getFechaToDate, enRangoDentroDeSemanaActual }
