import { initializeApp } from 'firebase/app';
import { getAuth, OAuthProvider, GoogleAuthProvider, FacebookAuthProvider, TwitterAuthProvider, signInWithRedirect, getRedirectResult   } from "firebase/auth";
import 'firebase/analytics'
import { getFirestore, collection, getDocs, doc, getDoc, where, query, addDoc, setDoc, deleteDoc } from 'firebase/firestore'
import AuthConst from './AuthConst'


const app = initializeApp(AuthConst.FIREBASE_OPTIONS);
const db = getFirestore(app);
const auth = getAuth();

const microsoftAuthProvider = new OAuthProvider('microsoft.com')
const googleAuthProvider = new GoogleAuthProvider()
const facebookAuthProvider = new FacebookAuthProvider()
const twitterAuthProvider = new TwitterAuthProvider()

let microsoftSignIn = () => {
  signInWithRedirect(auth, microsoftAuthProvider)
}

let googleSignIn = () => {
  signInWithRedirect(auth, googleAuthProvider)
}

let facebookSignIn = () => {
  signInWithRedirect(auth, facebookAuthProvider)
}

let twitterSignIn = () => {
  signInWithRedirect(auth, twitterAuthProvider)
}

let getAuthResult = () => {
  return new Promise((resolve, reject) => {
    getRedirectResult(auth)
      .then((result) => {
        const user = getUsuario(result.user);
        resolve({user})
      }).catch((error) => {
      // Handle Errors here.
      const errorCode = error.code;
      const errorMessage = error.message;
      // The email of the user's account used.
      const email = error.email;
      // The AuthCredential type that was used.
      reject(error)
    });
  })
}

let getActiveUser = (callback) => {
  auth.onAuthStateChanged((user) => {
    // USUARIO ACTIVO
    callback(user)
  })
}

let signOut = () => {
  auth.signOut()
}

function getUsuario(user) {
  if (!user)
    return null

  return {
    nombre: user.displayName,
    apellidoPaterno: null,
    apellidoMaterno: null,
    email: user.email,
    photoURL: user.photoURL
  }
}

const Firestore = { db, collection, getDocs, doc, getDoc, query, where, addDoc, setDoc, deleteDoc }
const Firebase = { Firestore, getAuthResult, googleSignIn, facebookSignIn, twitterSignIn, microsoftSignIn, getActiveUser, signOut }

export default Firebase
