<template>
  <v-app-bar v-if="$route.name != 'Login'" color="#eb2229" flat app dark>
    <v-app-bar-nav-icon @click="$navegador = !$navegador"/>
    <div class="v-toolbar-title text-xs-center align-center justify-center" style="flex-grow: 1">
      <v-img width="120px" height="53px" :src="require('../../assets/hapitLogo.svg')"/>
    </div>
    <v-spacer/>
    <v-btn v-if="$usuario.administrador" icon dark :to="{ name: 'Usuarios' }">
      <v-icon>mdi-account-group</v-icon>
    </v-btn>

    <v-menu v-if="$usuarioPuedeGestionarOrdenes()" offset-y>
      <template v-slot:activator="{ attrs, on }">
      <v-btn v-bind="attrs" v-on="on" icon dark>
        <v-badge v-if="$ordenesNuevas.length" color="green" :content="$ordenesNuevas.length"><v-icon>mdi-cart</v-icon></v-badge>
        <v-icon v-else>mdi-cart</v-icon>
      </v-btn>
      </template>

      <v-card>
        <template v-if="$ordenesNuevas.length > 0">
          <v-card>
            <v-card-text v-for="(orden, indexOrden) in $ordenesNuevas" :key="indexOrden">
              <v-row>
                <v-col cols="12">
                  <v-row>
                    <v-col cols="12" class="font-weight-bold">
                      Orden para {{ orden.alumno.nombreCompleto }}
                    </v-col>
                    <v-col cols="12" class="font-weight-bold">
                      Para consumir el {{ orden.fecha | fecha }}
                    </v-col>
                    <v-col cols="12" class="my-0 py-0">
                      <span class="green--text font-weight-bold">{{ orden.costo | moneda }}</span>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-card-text>
            <v-card-actions class="text-center" style="display: flex; justify-content: center">
              <v-btn small rounded color="primary" dark :to="{ name: 'ResumenCarrito' }">Ir al carrito</v-btn>
            </v-card-actions>
          </v-card>
        </template>

        <v-card-text v-else>
          <v-row align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
            <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
              <img src="../../assets/carrito_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
            </v-col>
            <v-col cols="12" class="text-center">
              <h2 style="font-family: Nunito;" class="text--disabled">AÃºn no tiene ordenes</h2>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-menu>

        <v-btn icon dark @click="cerrarSesion"><v-icon>mdi-exit-to-app</v-icon></v-btn>
  </v-app-bar>
</template>

<script>
  import AuthConst from '../../config/AuthConst'

  export default {
        name: "Bar",
      methods:{
        cerrarSesion () {
          localStorage.removeItem( AuthConst.ACCESS_TOKEN_KEY)
          localStorage.removeItem(AuthConst.CURRENT_USER_KEY)
          localStorage.removeItem(AuthConst.CARRITO_KEY)

          this.$usuario = {}
          this.$ordenesNuevas = []
          this.$router.push({ name: 'Login' })
        }
      }
    }
</script>

