import Vue from 'vue'
import FechaService from "../services/FechaService";

Vue.filter('moneda', function (value) {
  let formatted = parseFloat(value).toFixed(2)
  formatted = formatted.replace(/\B(?=(\d{3})+(?!\d))/g, ',')
  return '$ ' + formatted
});

Vue.filter('fecha', function (value) {
  return FechaService.getFechaString(value, 'DD MMMM YYYY');
});

