import Vue from 'vue'
import UsuariosService from "../services/UsuariosService";

let datosGlobales = new Vue({
    data: {
        $loader: false,
        $usuario: {},
        $alerta: { model: false, ancho: '', color: '', icon: '', mensaje: '', isSnackbar: false },
        $navegador: true,
        $ordenesNuevas: [],
        $cafeteriaSeleccionada: {}
    }
})

Vue.mixin({
    computed: {
        $loader: {
            get () { return datosGlobales.$data.$loader },
            set (newValue) { datosGlobales.$data.$loader = newValue }
        },
        $usuario: {
            get () { return datosGlobales.$data.$usuario },
            set (newValue) { datosGlobales.$data.$usuario = newValue }
        },
        $alerta: {
            get () { return datosGlobales.$data.$alerta },
            set (newValue) { datosGlobales.$data.$alerta = newValue }
        },
        $navegador: {
        get () { return datosGlobales.$data.$navegador },
        set (newValue) { datosGlobales.$data.$navegador = newValue }
        },
      $ordenesNuevas: {
        get () { return datosGlobales.$data.$ordenesNuevas },
        set(newValue) {
          datosGlobales.$data.$ordenesNuevas = newValue
        }
      },
      $cafeteriaSeleccionada: {
        get () { return datosGlobales.$data.$cafeteriaSeleccionada },
        set(newValue) {
          datosGlobales.$data.$cafeteriaSeleccionada = newValue
        }
      }
    },
    methods:{
        $getIndex (array, atributo, elementoABuscar) {
            return array.map(elemento => elemento[atributo]).indexOf(elementoABuscar[atributo])
        },
        $showAlerta (ancho = '600', color = 'gray', icon = 'mdi-checkbox-blank-circle', mensaje = '', isSnackbar = false, accion = null, img = null) {
            this.$alerta.model = false
            this.$nextTick(() => {
                this.$alerta.ancho = ancho
                this.$alerta.color = color
                this.$alerta.icon = icon
                this.$alerta.img = img
                this.$alerta.mensaje = mensaje
                this.$alerta.accion = accion
                this.$alerta.model = true
                this.$alerta.isSnackbar = isSnackbar

                if (this.$alerta.isSnackbar){
                    let vm = this
                    setTimeout(() => {
                        vm.$alerta.model = false
                    }, 800)
                }
            })
        },
      $showAlertMessage (ancho = '600', color = 'gray', icon = 'mdi-checkbox-blank-circle', mensaje = '', isSnackbar = false, accion = null, img = null,time = 3000) {
        this.$alerta.model = false
        this.$nextTick(() => {
          this.$alerta.ancho = ancho
          this.$alerta.color = color
          this.$alerta.icon = icon
          this.$alerta.img = img
          this.$alerta.mensaje = mensaje
          this.$alerta.accion = accion
          this.$alerta.model = true
          this.$alerta.isSnackbar = isSnackbar

          if (this.$alerta.isSnackbar){
            let vm = this
            setTimeout(() => {
              vm.$alerta.model = false
            }, time)
          }
        })
      },
        $getSaldoDeAlumnoParaCafeteria (idCafeteria, cafeteriasConSaldoAlumno) {
          const indexCafeteria = this.$getIndex(cafeteriasConSaldoAlumno, 'id', { id: idCafeteria })

          if (indexCafeteria >= 0)
            return cafeteriasConSaldoAlumno[indexCafeteria].saldoAlumno

          return 0
        },
      $alumnoPuedeOrdenar(){
          return UsuariosService.alumnoPuedeOrdenar(this.$usuario)
      },
      $usuarioPuedeGestionarOrdenes() {
          return UsuariosService.usuarioPuedeGestionarOrdenes(this.$usuario)
      }
    }
});

Vue.prototype.$getImgCategoria = (idCategoria) => 'https://images-hapit-lunch.s3.amazonaws.com/icons/' + idCategoria + '.png';
