import AuthConst from "@/config/AuthConst";
import http from "@/services/http";
import crudService from "@/components/services/CrudService";

const alumnoPuedeOrdenar = (usuario) => {
  return usuario.seccion.titulo === 'Bachillerato'
};

const usuarioPuedeGestionarOrdenes = (usuario) => {
  return usuario.esPadreDeFamilia || (usuario.esAlumno && alumnoPuedeOrdenar(usuario))
};

const getListaAlumnos = (usuario) => {
  if (usuario.esPadreDeFamilia)
    return usuario.hijos.map(hijo => hijo.alumno)
  else if (usuario.esAlumno)
    return [ usuario ]

  return []
};

const cambiarContrasena = (idEncargado, password) => {
  const formData = new FormData();
  formData.append("password", password);

  let url = AuthConst.BASE_URL + '/encargados/'+ idEncargado +'/password';
  return http.requestFormData(url, formData, 'patch')
};

const getInfoFromFirebase = (email) => {
  return new Promise((resolve, reject) => {
    crudService.buscarPorCampo('usuarios', email, 'email')
      .then(usuarios => {
        if (usuarios.length > 0)
          resolve(usuarios[0])

        else
          resolve({ email, deseaGuardarTarjeta: false })
      })
      .catch(e => (reject(e)))
  })
};

const saveInfoFromFirebase = (usuario) => {
  const savePromise = usuario.id ? crudService.actualizar : crudService.agregar;
  return savePromise('usuarios', usuario);
};

export default { alumnoPuedeOrdenar, usuarioPuedeGestionarOrdenes, getListaAlumnos, cambiarContrasena,
  getInfoFromFirebase, saveInfoFromFirebase }
