const requestFormData = (url, data, method, contentType = 'application/json') => {
  return new Promise((resolve, reject) => {
    axios({
      method,
      url,
      data,
      auth: hapitAuth,
      headers: {
        'Content-Type': contentType
      }
    }).then(function (response) {
      resolve(response.data)
    }).catch(function (error) {
      reject(error)
    })
  })
}

const multipart = (url, data) => {
  console.log(' sending .. request ',url, '   dat:',data   )
  return new Promise((resolve, reject) => {
    axios.post(url, data, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((response) => {
        resolve(response.data)
      })
      .catch((error) => {
        reject(error)      });
  })
}

export default { get, post, put, remove, patch, requestFormData, multipart }
