import Firebase from '../../config/Firebase'

const { db, collection, getDocs, doc, getDoc, query, where, addDoc, setDoc, deleteDoc } = Firebase.Firestore

const getCollection = (collectionName) => {
  return new Promise((resolve, reject) => {
    getDocs(collection(db, collectionName))
      .then(snapshot => {
        if (snapshot.empty) {
          resolve([])
        } else {
          let documentos = []
          snapshot.forEach(doc => {
            let documento = doc.data()
            documento.id = doc.id
            documentos.push(documento)
          })
          resolve(documentos)
        }
      })
      .catch(err => {
        reject(err)
      })
  })
}

const getDocument = (collectionName, idDoc) => {
  return new Promise((resolve, reject) => {
    const docRef = doc(db, collectionName, String(idDoc));
    getDoc(docRef)
      .then((doc) => {
        if (doc.exists()) {
          let documento = doc.data()
          documento.id = doc.id
          resolve(documento)
        } else {
          resolve(null)
        }
      })
      .catch(err => {
        reject(err)
      })
  })
}

const buscarPorCampo = (collectionName, valor, campo) => {
  return new Promise((resolve, reject) => {
    const q = query(collection(db, collectionName), where(campo, "==", valor));
    getDocs(q)
      .then(snapshot => {
        if (snapshot.empty) {
          resolve([])
        } else {
          let documentos = []
          snapshot.forEach(doc => {
            let documento = doc.data()
            documento.id = doc.id
            documentos.push(documento)
          })
          resolve(documentos)
        }
      })
      .catch(err => {
        reject(err)
      })
  })
}

const eliminar = (collectionName, idDoc) => {
  return new Promise((resolve, reject) => {
    deleteDoc(doc(db, collectionName, idDoc))
      .then(() => {
        resolve(idDoc)
      })
      .catch(err => {
        reject(err)
      })
  })
}

let agregar = (collectionName, documento) => {
  return new Promise((resolve, reject) => {
    addDoc(collection(db, collectionName), documento)
      .then((ref) => {
        documento.id = ref.id
        resolve(documento)
      })
      .catch(err => {
        reject(err)
      })
  })
}

let actualizar = (collectionName, documento) => {
  return new Promise((resolve, reject) => {
    setDoc(doc(db, collectionName, String(documento.id)), documento)
      .then(() => {
        resolve(documento)
      })
      .catch(err => {
        reject(err)
      })
  })
}

export default { getCollection, agregar, getDocument, buscarPorCampo, eliminar, actualizar }
